import React from "react";
import { Button, Form, Container, Row, Col, Stack } from "react-bootstrap";
import "./styles.css";

export default function App() {
  const [name, setName] = React.useState("");
  const [submitState, setSubmitState] = React.useState("");
  const isLowerCase = name === name.toLowerCase();
  const error = isLowerCase ? null : (
    <div class="alert alert-danger" role="alert">
      Usename must be lower case
    </div>
  );

  return (
    <div className="App">
      <Stack
        direction="horizontal"
        gap={3}
        className="col-md-8 mx-auto"
        style={{ marginTop: "3rem" }}
      >
        <Button
          disabled={Boolean(error)}
          onClick={(e) => {
            e.preventDefault();
            setSubmitState(name);
          }}
          variant="primary"
          type="submit"
          size="lg"
        >
          Submit
        </Button>
        <Form.Control
          type="name"
          placeholder="Your name"
          onChange={(event) => {
            setSubmitState("");
            setName(event.target.value);
          }}
          size="lg"
        />
      </Stack>
      <Stack className="col-md-8 mx-auto" style={{ marginTop: "1rem" }}>
        <div>{error}</div>
        {submitState !== "" && (
          <div class="alert alert-primary" role="alert">
            Hello, {submitState}
          </div>
        )}
      </Stack>
    </div>
  );
}
